import { Component, OnInit } from '@angular/core';
import {
    trigger,
    state,
    style,
    animate,
    transition,
  } from '@angular/animations';

@Component({
    selector: 'app-scroll-down-indecator',
    templateUrl: './scroll-down-indecator.component.html',
    styleUrls: ['./scroll-down-indecator.component.scss'],
    animations: [
        trigger('visibleAnimate', [
            state( 'visible', style({
                opacity: 1,
            })),
            state( 'invisible', style({
                opacity: 0,
            })),
            transition( 'visible <=> invisible', animate( '200ms ease-in') )
            // transition( 'inactive => active', animate( '200ms ease-in' ) ),
        ] )
    ],
})
export class ScrollDownIndecatorComponent implements OnInit {

    state: string = 'visible';

    constructor() { }

    ngOnInit() {
    }

    invisible() {
        this.state = 'invisible';
        console.log( 'invisible' );
    }
}
