import { CoverPhotoComponent } from './cover-photo/cover-photo.component';
import { Component, ViewEncapsulation, OnInit, AfterViewInit, ViewChild, HostListener } from '@angular/core';
import { ScrollDownIndecatorComponent } from './scroll-down-indecator/scroll-down-indecator.component';
import { EventManager } from '@angular/platform-browser';

declare const $: any;

@Component({
    selector: 'app-root',
    templateUrl: './app.component.html',
    styleUrls: ['./app.component.scss'],
    encapsulation: ViewEncapsulation.None,

})
export class AppComponent implements OnInit, AfterViewInit {
    title = 'eunyul-first-birthday';


    @ViewChild(ScrollDownIndecatorComponent, { static: true }) scrollDownIndicator: ScrollDownIndecatorComponent;
    @ViewChild(CoverPhotoComponent, { static: true }) coverPhoto: CoverPhotoComponent ;

    constructor( private readonly eventManager: EventManager ) {

    }

    ngOnInit() {
        // $( 'body' ).scrollspy({ target: '#navbar-example', offset: 200 });

        // Scroll Down 하라는 표시 제거
        setTimeout(() => {
            this.scrollDownIndicator.invisible();
        }, 3000);

        // this.eventManager.addEventListener( document, 'aos:in', ({ detail }) => {
        //     console.log('animated in', detail);
        // } );

        // this.eventManager.addEventListener( document, 'aos:out', ({ detail }) => {
        //     console.log('animated in', detail);
        // } );

        document.addEventListener('aos:in:coverPhoto', ( event ) => {
            console.log('animated in', event);
            this.coverPhoto.show();
        });

        document.addEventListener('aos:out:coverPhoto', ( event ) => {
            console.log('animated out', event);
            this.coverPhoto.hide();
        });

    }

    ngAfterViewInit() {
    }


    // @HostListener( 'aos:in', [ '$event' ] )
    // onDocumentAosIn( event ) {
    //     console.log( 'event1: ', event );
    //     console.log( 'event2: ', event.detail );
    // }

    // @HostListener( 'aos:out', [ '$event' ] )
    // onDocumentAosOut( event ) {
    //     console.log( 'event1: ', event );
    //     console.log( 'event2: ', event.detail );
    // }
}
